*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul, ol, li {
  list-style: none;
}

img {
  vertical-align: top;
}

button {
  cursor: pointer;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit;
}

html, body {
  height: 100%;

}

body {
  line-height: 1;
  font-family: 'Kanit', sans-serif;
  font-size: 18px;
  color: black;
}
/*==============Власноруч============*/
textarea:focus {
  outline: none;
}
